:root
{
  --yellow: #F5C32C;
    --orange : #feb837;
    --black : #242D49;
    --gray : #788097;
    --blueCard : #DDF8FE;
    --purple: rgb(238 210 255);
    --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
    --orangeCard: rgba(252, 166, 31, 0.45);
    --smboxShadow: -79px 51px 60px rgba(142, 34, 34, 0.08);
}
.App{
  padding: 0.5rem 3.5rem;
  overflow: hidden;
  color: var(--black); 
  font-family: sans-serif; 
  background: linear-gradient(rgb(221, 221, 238),rgb(239, 219, 219));  
  opacity: 1;
  transform: translateY(0);
  height: fit-content;
}
*{
  font-weight: 400;
}
.button{
  margin: 0px 10px;
  width: fit-content;
  height: fit-content;
  border-radius: 34px;
  border: none;
  color: white;
  font-size: 16px;
  padding: 11px 26px  11px 26px;
  background: linear-gradient(180deg,#fdc50f 26.71%,#fb982f 99.36%);
  box-shadow: 0px 20px 24px 3px rgba(251,161,40,0.42);
  &:hover{
    background: white;
    cursor: pointer;
    color: var(--orange);
    border: 1px solid orange;
  }
}
.disable{
  box-shadow: 0px 20px 24px 3px rgba(251,161,40,0.42);
  background: rgb(190, 128, 13);
}
.line{
  background-color: var(--yellow);
  width: 100%;
  height: 2px;
  margin-top: 2rem;
  box-shadow: 0px 20px 24px 3px rgba(7, 5, 1, 0.42);
}

@media screen and (max-width: 480px) {
  .App{
    padding: 0.5rem 1rem;
  }
}
